<template>
  <div class="login">
    <Header></Header>
    <div class="box-main">
      <p>登录系统</p>
      <div class="mainContent">
        <div class="div-bor">
          <span class="mintui mintui-account grey"></span>
          <input placeholder="请输入手机号" type="number" value="18516147971"/>
        </div>
        <div class="div-bor">
          <span class="mintui mintui-password grey"></span>
          <input style="width: 130px;" placeholder="请输入验证码" type="number" value="6753"/>
          <mt-button type="danger" size="small">验证码</mt-button>
        </div>
        <div class="div-submit">
          <mt-button type="primary" size="small" style="width: 220px;" @click="loginSubmit">登录</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import { Toast, Indicator  } from 'mint-ui'
export default {
  name: 'Login',
  components: { Header },
  data () {
    return {
    }
  },
  methods: {
    loginSubmit() {
      Indicator.open('登录中...')
      setTimeout(() => {
        Toast({
          message: '登录成功',
          position: 'bottom'
        })
        sessionStorage.setItem('_userInfo', 'pjs')
        Indicator.close()
        this.$router.push('/')
      }, 3000)
    }
  },
  mounted() {
  }
}
</script>
<style scoped>
  .grey {
    color: silver;
    font-size: 20px;
  }
  .box-main {
    border: 1px solid #d2d2d2;
    margin: 15px 2%;
    padding: 0px 10px 10px 10px;
  }
  .mainContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
  }
  .div-bor {
    position: relative;
    margin-bottom: 25px;
  }
  .div-submit {
    margin-bottom: 50px;
  }
  .div-bor span {
    position: absolute;
    top: 8px;
    margin-left: 3px;
  }
  .div-bor input {
    width: 200px;
    padding: 5px 0px 5px 22px;
    line-height: 25px;
    border-radius: 5px;
    border: 1px solid #d2d2d2;
  }
</style>



